<script>
import {CodeToText, regionDataPlus} from "element-china-area-data";

export default {
  name: "MoveIntoManagemen.vue",
  data() {
    let bmi_mer_name = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('商户名称不能为空'));
      } else {
        callback();
      }
    };
    let bmi_mer_alias = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('商户简称不能为空'));
      } else {
        callback();
      }
    };
    let bms_license_code = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('统一社会信用代码不能为空'))
      } else if (value.trim().length !== 18) {
        callback(new Error('请输入正确的统一社会信用代码!'))
      } else {
        callback()
      }
    };
    let bmi_industry_id = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('商户所属行业不能为空'));
      } else {
        callback();
      }
    };
    let checkList = (rule, value, callback) => {
      if (value.length === 0) {
        callback(new Error('每周营业日不能为空'));
      } else {
        callback();
      }
    };
    let bms_work_t1 = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('营业时间段不能为空'));
      } else {
        callback();
      }
    };
    let bms_work_t2 = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(' '));
      } else {
        callback();
      }
    };
    let selectedOptions = (rule, value, callback) => {

      if (this.selectedOptions.length !== 3 && (this.selectedOptions[0] === '' || this.selectedOptions[1] === '' || this.selectedOptions[2] === '') ) {
        callback(new Error('所在地区不能为空'));
      } else {
        callback();
      }
    };
    let bms_address = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('详细地址不能为空'));
      } else {
        callback();
      }
    };
    let bmi_login_id = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('商户登录名（手机号）不能为空'))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('请输入正确的商户登录名（手机号）!'))
      } else {
        callback()
      }
    };
    let bmi_password = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('商户初始密码不能为空'))
      } else {
        callback()
      }
    };
    let bms_name = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('门店名称不能为空'));
      } else {
        callback();
      }
    };
    let bms_alias = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('门店简称不能为空'));
      } else {
        callback();
      }
    };
    return {
      src1: '',
      src2: '',
      src3: '',
      src4: '',
      checkList: [],
      dialogVisible1:false,
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      value1: [],
      activeIndex: '1',
      tableData: [],
      dialogVisible: false,
      shopForm: {
        bmi_mer_name: '',
        bmi_mer_alias: '',
        bmi_login_id: '',
        bmi_password: '',
        bms_address: '',
        bms_license_code: '',
        bmi_industry_id: '',
        bms_work_t1: '',
        bms_work_t2: '',
        bms_uuid:'',
        bms_name:'',
        bms_alias:''
      },
      shopFormrules: {
        bmi_mer_name: [
          {required: true, validator: bmi_mer_name, trigger: 'blur'}
        ],
        bmi_mer_alias: [
          {required: true, validator: bmi_mer_alias, trigger: 'blur'}
        ],
        bms_license_code: [
          {required: true, validator: bms_license_code, trigger: 'blur'}
        ],
        bmi_industry_id: [
          {required: true, validator: bmi_industry_id, trigger: 'blur'}
        ],
        checkList: [
          {required: true, validator: checkList, trigger: ['blur','change']}
        ],
        bms_work_t1: [
          {required: true, validator: bms_work_t1, trigger: 'blur'}
        ],
        bms_work_t2: [
          {required: true, validator: bms_work_t2, trigger: 'blur'}
        ],
        selectedOptions: [
          {required: true, validator: selectedOptions, trigger: ['blur','change']}
        ],
        bms_address: [
          {required: true, validator: bms_address, trigger: 'blur'}
        ],
        bmi_login_id: [
          {required: true, validator: bmi_login_id, trigger: 'blur'}
        ],
        bmi_password: [
          {required: true, validator: bmi_password, trigger: 'blur'}
        ],
        bms_name: [
          {required: true, validator: bms_name, trigger: 'blur'}
        ],
        bms_alias: [
          {required: true, validator: bms_alias, trigger: 'blur'}
        ],
        loc:[
          {

            required: true,
            // 这里有两种处理,一种是自定义验证,拿到值后自己对属性进行验证,比较麻烦
            validator : (rule, value, callback) => {

              if (this.selectedOptions.length===0 ) {
                callback(new Error('请选择所在地区'));
              } else {
                callback();
              }
            },
            trigger: ['change','blur'],

          }
        ],
        week:[
          {

            required: true,
            // 这里有两种处理,一种是自定义验证,拿到值后自己对属性进行验证,比较麻烦
            validator : (rule, value, callback) => {

              if (this.checkList.length===0 ) {
                callback(new Error('请选择每周营业日'));
              } else {
                callback();
              }
            },
            trigger: ['change','blur'],

          }
        ],

      },
      tableData_industry: [],//行业表格数据
      selectedOptions: [],
      selectedOptions1:[],
      options: regionDataPlus,
      pca: {
        province: '',//省
        city: '',//市
        area: '',//县
      },
      bmi_status:'',
      shop_id:'',
      bcfh_id:'',
      bcf_manager_id:'',
      bcf_manager_name:'',
      bcf_type:'',
      audit: false,
      shop_deactivate:false,
      shop_enable:false,
      bmti_check_status:'',
      shopForm1:{

      }
    }
  },
  created() {
    this.bmi_status='1';
    this.Select_shop();
    this.audit = true;
  },
  methods: {
    /** 电话号校验 */
    isCellPhone(val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false
      } else {
        return true
      }
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.Select_shop();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.Select_shop();
    },
    handleSelect(key, keyPath) {
      console.log('上：' + key, keyPath);
      if(key==='1'){
        this.bmi_status='1';
        this.audit = true;
        this.shop_deactivate = false;
        this.shop_enable = false;
      }else if(key==='2'){
        this.bmi_status='2';
        this.audit = false;
        this.shop_deactivate= true;
        this.shop_enable = false;
      }else if(key==='3'){
        this.bmi_status='4';
        this.audit = false;
        this.shop_deactivate= false;
        this.shop_enable = true;
