        console.log("异步调用成功返回:" + JSON.stringify(res));
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">商户启用成功</span>'
          });
          this.enable_shop_Select();
        } else {
          console.info("商户启用失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        console.log("异步调用出错:" + JSON.stringify(error));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商户启用失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商户启用失败！', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    deactivate_shop(index,row){
      this.bmi_status='4';
      this.shop_id=row.bmi_uuid;
      this.audit_shop1();
      this.bmti_check_status='7';
      this.shop_coupon_status();
    },
    deactivate_shop_Select(){
      this.queryParam.currentPage= 1;
      this.bmi_status='2';
      this.Select_shop();
    },
    enable_shop(index,row){
      this.bmi_status='2';
      this.shop_id=row.bmi_uuid;
      this.audit_shop2();
      this.bmti_check_status='8';
      this.shop_coupon_status();
    },
    enable_shop_Select(){
      this.queryParam.currentPage= 1;
      this.bmi_status='4';
      this.Select_shop();
    },
    shop_coupon_status(){
      let that = this;
      let _jsondata_ = {
        bmti_check_status: this.bmti_check_status,
        bmti_mer_id:that.shop_id,
        bmti_bank_center_id_child: that.customGlobal.userInfoExt.cui_bank_center_id,
        bmti_province_id_child: that.customGlobal.userInfoExt.cui_province_id,
        bmti_city_id_child: that.customGlobal.userInfoExt.cui_city_id,
        bmti_county_id_child: that.customGlobal.userInfoExt.cui_county_id,
        bmti_station_id_child: that.customGlobal.userInfoExt.cui_station_id,
      };

      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a7 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.shop_coupon_status,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a7.then((res) => {
        console.log("异步调用成功返回:" + JSON.stringify(res));
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">商户券状态修改成功</span>'
          });
        } else {
          console.info("商户券状态修改失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        console.log("异步调用出错:" + JSON.stringify(error));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商户券状态修改!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商户券状态修改！', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    }
  }
}
</script>

<style scoped>
.account-plat-fixed-right /deep/ .el-table__fixed-right {
  height: 100% !important;
}

.account-plat-fixed /deep/ .el-table__fixed {
  height: 100% !important;
}
</style>



